import m from 'mithril'

import Stream from './stream.js'
import bss from 'bss'

const validChar = (c, ci) => ci ? x => c.indexOf(x.toLowerCase()) > -1 : x => c.indexOf(x) > -1
    , isSeparator = validChar('\n\t ;')
    , cache = new Map()
    , state = {}
    , S = {}

let routing = false

export default s

function s(a, ...rest) {
  if (typeof a === 'string')
    throw new Error('Are you trying to do hyperscript? Here we sin! Try writing s`' + a + '`(...) instead')

  const isTagged = Array.isArray(a) && Array.isArray(a.raw)
      , comp = !isTagged && { fn: a, waiting: rest[0] }
      , x = tagged(({ tag, styles, args }) => call({ S, comp, tag, styles, args }))

  if (isTagged) {
    const { s } = parse(a)
    const fn = x(...arguments)
    fn.S = S
    fn.styles = s
    fn.args = rest
    return fn
  }

  return typeof a === 'object' && typeof a.then === 'function'
    ? m(promiser, { waiting: rest[0] }, a)
    : x
}

s.bss = bss
s.stream = Stream
s.redraw = m.redraw
s.trust = m.trust
s.request = m.request
s.fragment = m.fragment

s.pathmode = ''
s.mount = mount

Object.defineProperty(s, '_init', {
  value: init,
  iterable: false
})

Object.defineProperty(s, 'dev', {
  get() { return bss.dev },
  set(value) { bss.dev = value }
})

function mount(dom, fn, attrs = {}) {
  if (typeof dom === 'function') {
    fn = dom
    dom = document.body
    attrs = fn || {}
  }

  if (s.pathmode[0] === '#' && window.location.hash.indexOf(s.pathmode + '/') !== 0)
    window.location.hash = s.pathmode + '/'
  if (s.pathmode[0] === '?' && window.location.search.indexOf(s.pathmode + '/') !== 0)
    window.location.search = s.pathmode + '/'

  m.mount(dom, {
    view: init(window.location, fn, attrs).view
  })
}

function init(url, fn, attrs = {}) {
  attrs.route = router('', {
    url,
    notFound: s.stream(),
    title: s.stream(window.document.title || ''),
    head: s.stream()
  })
  attrs.route.title.map(x => window.document.title = x)
  return {
    view: () => call(fn(attrs), attrs),
    route: attrs.route
  }
}

function call(a, args) {
  return !a
    ? a
    : Array.isArray(a)
    ? a.map(x => call(x, args))
    : a && typeof a.then === 'function'
    ? m(promiser, args, a)
    : typeof a === 'function'
    ? call(a.constructor === Stream ? a() : a(args), args)
    : a.S === S
    ? a.comp
      ? component(a)
      : vnode(a)
    : a
}

function vnode({ tag, styles, classes = [], args }) {
  const { attrs, children } = normalize(args)
  addClasses(attrs, { styles, classes })
  return m(tag || 'div', attrs, children.map(x => call(x)))
}

function component(x) {
  const { attrs, children } = normalize(x.args)
  return m(cheater, attrs, x, children.map(x => call(x)))
}

function cheater({ attrs, children: [x, ...rest] }) {
  const streamAttrs = {}
  let life

  let view = x.comp.fn({
    ...updateAttrs(streamAttrs, attrs), life: fn => life = fn
  }, rest)

  if (view !== 'function' && typeof view.then === 'function') {
    view.then(x => {
      view = x
      s.redraw()
    })
  }

  const component = {
    oninit: () => view,
    view: ({ attrs, children }) => {
      rest.splice(0, children.length - 1, ...children.slice(1))
      updateAttrs(streamAttrs, attrs)
      return view !== 'function' && typeof view.then === 'function'
        ? x.comp.waiting
        : view.tag
        ? cheaterView(x.comp.fn(attrs, children.slice(1)), [], x)
        : cheaterView(call(view(children.slice(1))), children, x)
    }
  }

  life && giveLife(component, [].concat(life))
  return component
}

function cheaterView(vnode, children, x) {
  if (typeof vnode !== 'object' && x.tag)
    vnode = m(x.tag, children[1])
  vnode.tag && x.tag && (vnode.tag = x.tag)
  if (x.styles.length) {
    vnode.attrs = vnode.attrs || {}
    addClasses(vnode.attrs, x)
  }

  return vnode
}

function addClasses(attrs, { styles, classes = [] }) {
  if (styles.length || classes.length)
    attrs.className = [].concat(
      classes,
      (attrs.className || []),
      styles.map(({ xs, args }) =>
        bss(xs, ...args.map(x => [].concat(x).map(x => x.S === S ? bss(x.styles, ...x.args) : x)))
      )
    ).join(' ')
}

function updateAttrs(previous, next) {
  return Object.keys(next).reduce((acc, k) => {
    if (k === 'key') {
      previous[k] = next[k]
    } else {
      previous[k]
        ? typeof next[k] !== 'function' && previous[k]() !== next[k] && previous[k](next[k])
        : (previous[k] = typeof next[k] === 'function' ? (previous[k] || next[k]) : s.stream(next[k]))
    }
    return previous
  }, previous)
}

function normalize(children) {
  const attrs = children[0] && typeof children[0] === 'object' && !Array.isArray(children[0]) && children[0].S !== S
    ? children.shift()
    : {}

  const life = [].concat(attrs.life || [])

  if (attrs.href && String(attrs.href).indexOf('://') === -1)
    link(attrs, life)

  life.length && giveLife(attrs, life)
  delete attrs.life

  return { attrs, children }
}

function giveLife(component, life) {
  return Object.assign(component, {
    oncreate: (v) => v.state && (v.state.rm = life.map(x => x(v.dom)).filter(x => typeof x === 'function')),
    onremove: (v) => v.state && v.state.rm && v.state.rm.map(x => x(false)),
    onbeforeremove: (v) => v.state && v.state.rm.length && Promise.all(v.state.rm.map(x => x(true))).then(() => v.state.rm = null)
  })
}

function promiser({ attrs, children }) {
  let view
  let settled = false
  children[0].then(x => {
    view = x
    settled = true
    m.redraw()
  })

  return {
    oninit: () => children[0],
    view: () => settled ? call(view, attrs) : attrs.waiting
  }
}

function tagged(fn, {
  tag,
  styles = []
} = {}) {
  return function(xs, ...args) {
    if (Array.isArray(xs) && Array.isArray(xs.raw)) {
      const { t, s } = parse(xs)
      return tagged(fn, {
        tag: t || tag,
        styles: s ? styles.concat({ xs: s, args }) : styles
      })
    }

    return fn({ S, tag, styles, args: Array.from(arguments) })
  }
}

function parse(xs) {
  if (cache.has(xs))
    return cache.get(xs)

  let i = 0
    , char = xs[0][i]

  while (char && !isSeparator(char)) {
    char = xs[0][++i]
  }

  const tag = char !== ' ' && xs[0].slice(0, i)
  const rest = tag ? xs[0].slice(i + 1) : xs[0]
  const result = {
    t: tag,
    s: (rest.trim() || xs.length > 1) && [rest].concat(xs.slice(1))
  }

  cache.set(xs, result)

  return result
}


function sanitize(x) {
  return x === '*' ? ['*'] : x.replace(/[/]?/, '/').split(/(?=[/*])/)
}

function getScore(match, current) {
  return match.reduce((acc, x, i) =>
    acc + ( x === '404'
      ? 0 : x === current[i]
      ? 4 : x[1] === ':' && current[i] && current[i].length > 1
      ? 3 : x === '/' && !current[i]
      ? 2 : x === '*'
      ? 1 : 0)
  , 0)
}

function params(path, current) {
  return path.reduce((acc, x, i) => {
    x[1] === ':' && (acc[x.slice(2)] = current[i].slice(1))
    return acc
  }, {})
}

function link(attrs, life) {
  life.push(dom => dom.addEventListener('click', e => {
    if (
      !e.defaultPrevented &&
      (e.button === 0 || e.which === 0 || e.which === 1) &&
      (!e.currentTarget.target || e.currentTarget.target === '_self') &&
      !e.ctrlKey && !e.metaKey && !e.shiftKey && !e.altKey
    ) {
      e.preventDefault()
      window.history.pushState(null, null, String(attrs.href))
      state[String(attrs.href).slice(s.pathmode.length)] = attrs.href.state
      m.redraw()
    }
  }))
}

function reroute(path, options = {}) {
  s.pathmode[0] === '#'
    ? window.location.hash = s.pathmode + path
    : s.pathmode[0] === '?'
    ? window.location.search = s.pathmode + path
    : window.history[options.replace ? 'replaceState' : 'pushState'](null, null, s.pathmode + path)
  state[path] = options
  s.redraw()
}

function getPath(location, x = 0) {
  return s.pathmode[0] === '#'
    ? location.hash.slice(s.pathmode.length + x)
    : s.pathmode[0] === '?'
    ? location.search.slice(s.pathmode.length + x)
    : location.pathname.slice(s.pathmode + x)
}

function router(root, attrs) {
  Object.assign(route, attrs)
  route.link = (x, state) => ({ toString: () => s.pathmode + root + (x[0] === '/' ? x : '/' + x), state })
  route.has = x => x === '/' ? route() === root + x : route().indexOf(root + x) === 0

  return route

  function route(routes, options = {}) {
    if (typeof routes === 'undefined')
      return getPath(route.url)

    if (typeof routes === 'string')
      return reroute(root + (routes[0] === '/' ? routes : '/' + routes), options)

    if (!routing) {
      routing = true
      s.pathmode[0] === '#'
        ? window.addEventListener('hashchange', () => s.redraw())
        : typeof window.history.pushState === 'function' && window.addEventListener('popstate', () => s.redraw())
    }

    const path = getPath(route.url, root.length)
    const current = sanitize(path)
    const [ , match, view = options.notFound] = Object
      .entries(routes)
      .reduce((acc, [match, view]) => {
        match = sanitize(match)
        const score = getScore(match, current)
        return score > acc[0]
          ? [score, match, view]
          : acc
      }, [0])

    const next = root + (match && match[0] !== '*'
      ? match.map((x, i) => current[i]).join('')
      : '')

    if (view === undefined || options.notFound)
      route.notFound(true)

    return m.fragment(
      m.fragment({ key: next }, view && call(view, {
        ...(root + path === next && state[root + path] || {}),
        ...params(match || [], current),
        route: router(next.replace(/\/$/, ''), attrs)
      }))
    )
  }
}
